import React from 'react'

export default class PriceSlider extends React.Component {
    constructor(props) {
        super(props)

        this.leftCircle = React.createRef()
        this.rightCircle = React.createRef()
        this.line = React.createRef()
        this.coloredLine = React.createRef()

        this.state = {
            rightCircleTop: 0,
            rightCircleLeft: 200,
            leftCircleTop: 0,
            leftCircleLeft: 0,
            coloredLineWidth: 0,
            coloredLineLeft: 0,
            totalWidth: 0
        }
    }

    componentDidMount() {
        this.setState({
            totalWidth: this.line.current.getBoundingClientRect().width
        }, () => { console.log(this.state) })
    }

    onInput = (e, key) => {
        //(e) => this.props.onFilterChange('minPrice', e.target.value
    }

    handleContainerClick = (e) => {
        const mX = e.clientX //mouseX
        const leftRect = this.leftCircle.current.getBoundingClientRect()
        const rigthRect = this.rightCircle.current.getBoundingClientRect()
        const lineRect = this.coloredLine.current.getBoundingClientRect()

        const lX = leftRect.left
        const rX = rigthRect.left + rigthRect.width

        const lOffset = this.leftCircle.current.offsetLeft + this.leftCircle.current.offsetWidth
        //const rX = rigthRect.left + rigthRect.width

        const distLR = rigthRect.left - (leftRect.left + leftRect.width)
        const center = leftRect.left + leftRect.width + distLR / 2

        let stepLeft = 0
        let stepRight = 0
        if (mX < lX) {
            stepLeft = -5
        } else if (mX > lX & mX < center) {
            stepLeft = 5
        } else if (mX > center & mX < rX) {
            stepRight = -5
        } else if (mX > center & mX > rX) {
            stepRight = 5
        }

        let lineLeft = (lOffset + stepLeft) + leftRect.width - 3
        let lineWidth = distLR + stepLeft + stepRight


        console.log(e)
        this.setState(prevState => {
            return {
                leftCircleLeft: prevState.leftCircleLeft + stepLeft,
                rightCircleLeft: prevState.rightCircleLeft + stepRight,
                coloredLineLeft: lineLeft,
                coloredLineWidth: lineWidth
            }
        })
    }

    render() {
        return (
            <div className="price-slider">
                <input type='range' multiple />
                <div className="circle-container" onClick={this.handleContainerClick}>
                    <div className="circle-1" style={{left: this.state.leftCircleLeft}} ref={this.leftCircle}></div>
                    <div className="line-white" ref={this.line}></div>
                    <div className="line-colored" style={{ width: this.state.coloredLineWidth, left: this.state.coloredLineLeft }} ref={this.coloredLine}></div>
                    <div className="circle-2" style={{left: this.state.rightCircleLeft}} ref={this.rightCircle}></div>
                </div>
                <div className="counter">
                    <input type="text" className="input-1" value={this.props.minPrice} onChange={(e) => this.onInput(e, 'minPrice')} />
                    <div className="input-separator"></div>
                    <input type="text" className="input-2" value={this.props.maxPrice} onChange={(e) => this.onInput(e, 'minPrice')} />
                </div>
            </div>
        )
    }
}


